import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  paper2:{padding:'20px',
  marginTop:'20px',
  backgroundColor:'#4EB1BA',
 // backgroundImage: `url("http://localhost:3000/HowItWorks.png")`
},
  title: {
          
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  number: {
    fontSize: 24,
    fontFamily: theme.typography.fontFamily,
    color: theme.palette.secondary.main,
    fontWeight: theme.typography.fontWeightMedium,
  },
  text: {
   
    fontFamily: theme.typography.fontFamily,
    color: theme.palette.text.secondary.main,
    fontWeight: theme.typography.fontWeightMedium,
  },

  number2: {
   
    fontFamily: theme.typography.fontFamily,
    color: theme.palette.secondary.main,
    fontWeight: theme.typography.fontWeightMedium,
  },
   image: {
    height: 55,
    marginTop: theme.spacing(4),
    marginBottom: theme.spacing(4),
  },
  title2: {
    marginBottom: theme.spacing(7),
  },
  }));

  export default function HowItWorks(){
    const classes = useStyles();
return(
  <Paper className={classes.paper2}>
    <center>
    <Typography variant="h4" marked="center" className={clsx(classes.title2, classes.number2)} component="h2">
          How It Works<hr width="12%" color="theme.palette.secondary.main"/>
        </Typography>
        
    </center>
  <div className={classes.root}>

        <Grid container spacing={3}>
        <Grid item xs>
          <Paper className={classes.paper}> <div className={classes.number}>1.</div>
          <img
                  src="http://localhost:3000/Customize-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
          <Typography variant="h5" align="center" className={classes.text}>
                 Customize Our Inventory.
                </Typography>
          </Paper>
        </Grid>
        <Grid item xs>
          <Paper className={classes.paper}><div className={classes.number}>2.</div>
          <img
                   src="http://localhost:3000/TrainerNeed-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
          <Typography variant="h5" align="center">
               Determine Trainers Need.
                </Typography>
          </Paper>
        </Grid>
        <Grid item xs>
          <Paper className={classes.paper}><div className={classes.number}>3.</div>
          <img
                  src="http://localhost:3000/PackageList-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
          <Typography variant="h5" align="center">
                 Import/Export Packages List.
                </Typography>
                </Paper>
        </Grid>
      </Grid>
    </div>
    <div className={classes.root}>

<Grid container spacing={3}>
<Grid item xs>
  <Paper className={classes.paper}><div className={classes.number}>4.</div>
  <img
                   src="http://localhost:3000/FirstCome-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
  <Typography variant="h5" align="center">
                  First come, first served,so be quick.
                </Typography>
                </Paper>
</Grid>
<Grid item xs>
  <Paper className={classes.paper}><div className={classes.number}>5.</div>
  <img
                   src="http://localhost:3000/Damage-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
  <Typography variant="h5" align="center" >
                  Damage Product Details.
                </Typography>
                </Paper>
</Grid>
<Grid item xs>
  <Paper className={classes.paper}><div className={classes.number}>6.</div>
  <img
                   src="http://localhost:3000/24-7-Icon.png"
                  alt="suitcase"
                  className={classes.image}
                />
  <Typography variant="h5" align="center">
                  24*7 Available.
                </Typography>
                </Paper>
</Grid>
</Grid>
</div>
    </Paper>
)
  }