  const useStyles = makeStyles(theme => ({
    root: {
      display: 'flex',
      backgroundColor: "#fce4ec",
    },
    paper:{padding:'20px',marginTop:'20px',backgroundColor: "#fce4ec"},
    container: {
      marginTop: theme.spacing(8),
      marginBottom: theme.spacing(8),
      display: 'flex',
    },
    iconsWrapper: {
      height: 120,
    },
    icons: {
      display: 'flex',
    },
    icon: {
      width: 48,
      height: 48,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
     backgroundColor: "#ffc071",
      marginRight: theme.spacing(1),
      '&:hover': {
      backgroundColor: "#ffb25e",
      },
    },
    list: {
      margin: 0,
      listStyle: 'none',
      paddingLeft: 0,
    },
    listItem: {
      paddingTop: theme.spacing(0.5),
      paddingBottom: theme.spacing(0.5),
    },
    language: {
      marginTop: theme.spacing(1),
      width: 150,
    },
  }));
 
  export default function AppFooter() {
    const classes = useStyles();
  
    return (
        <div>
     
      <Grid container spacing={3} className={classes.root}>
       
        <Grid item xs={6}>
          <div className={clsx(classes.paper)}>
          
        <Container className={classes.container}>
          <Grid container spacing={5}>
            <Grid item xs={6} sm={4} md={3}>
              <Grid
                container
                direction="column"
                justify="flex-end"
                className={classes.iconsWrapper}
                spacing={2}
              >
               
                <Grid item className={classes.icons}>
                <a href="" className={classes.icon}>
                    <FacebookIcon/>
                  </a>
                  <a href="" className={classes.icon}>
                    <TwitterIcon/>
                </a>
                <a href="" className={classes.icon}>
                <InstagramIcon/>
                </a>
                <a href="" className={classes.icon}>
                <LinkedInIcon/>
                </a>

                </Grid>
               
                <br/>
                
              </Grid>
               <Grid item xs={12}> 
               
                  <Copyright />
               
                 </Grid> 
            </Grid>
          
           
          </Grid>
        </Container>
     
          </div>
        </Grid>
        <Grid item xs={6}>
          <Paper className={classes.paper}>
        
            <h3>Find Us.</h3><br/>
            Feel free to contact us through any medium!<br/><br/>
            <LocationOnIcon/>Top Floor Reynold's Block, ITM GOI, Gwalior, M.P. 474001.
            <br/><br/>
            <PhoneIcon/> +91-8305990550<br/><br/>
            <MailIcon/>contact@fizrobotics.com<br/><br/>
            <MailIcon/> business@fizrobotics.com<br/><br/>
            <br/>
            
          </Paper>
        </Grid>
        
      </Grid>
      </div>
    );
    
  }