import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import AssignmentIcon from '@material-ui/icons/Assignment';
import {BaseUrl,getData} from '../component/FetchServices'

import Trainer from '../component/Trainer';
import DisplayAllTrainer from '../component/DisplayAllTrainer';
import Product from '../component/Product';
import DisplayAllProduct from '../component/DisplayAllProduct';
import TransitionIn from '../component/TransitionIn';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import AddSchool from '../component/AddSchool';
import DisplayAllSchool from '../component/DisplayAllSchool';
import DisplayFeedback from '../component/DisplayFeedback';
import DisplaySchoolModule from '../component/userSide/DisplaySchoolModule';
import SchoolModule from '../component/userSide/SchoolModule';
import Order from '../component/Order';
import DamageFileShow from '../component/DamageFileShow';

export default function MainListItems(props){
const handleClick=(view)=>{
  props.changeView(view)
}


const mainListItems = (
  <div>
    <ListItem button onClick={()=>handleClick(<Trainer/>)}>
      <ListItemIcon>
        <PersonAddIcon />
      </ListItemIcon>
      <ListItemText primary="Add Trainer" />
    </ListItem>
    <ListItem button onClick={()=>handleClick(<DisplayAllTrainer/>)}>
      <ListItemIcon>
        <PeopleIcon />
      </ListItemIcon>
      <ListItemText primary="List Trainer" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<Product/>)}>
      <ListItemIcon>
        <ShoppingCartIcon />
      </ListItemIcon>
      <ListItemText primary="Add Product" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<DisplayAllProduct/>)}>
      <ListItemIcon>
        <BarChartIcon />
      </ListItemIcon>
      <ListItemText primary="List Product" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<AddSchool/>)}>
      <ListItemIcon>
        <ShoppingCartIcon />
      </ListItemIcon>
      <ListItemText primary="Add School" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<DisplayAllSchool/>)}>
      <ListItemIcon>
        <BarChartIcon />
      </ListItemIcon>
      <ListItemText primary="Display All School" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<SchoolModule/>)}>
      <ListItemIcon>
        <BarChartIcon />
      </ListItemIcon>
      <ListItemText primary="School-Product" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<DisplaySchoolModule/>)}>
      <ListItemIcon>
        <BarChartIcon />
      </ListItemIcon>
      <ListItemText primary="Display Module" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<TransitionIn/>)}>
      <ListItemIcon>
        <LayersIcon />
      </ListItemIcon>
      <ListItemText primary="Transition In" />
    </ListItem>

    
    <ListItem button onClick={()=>handleClick(<DisplayFeedback/>)}>
      <ListItemIcon>
        <LayersIcon />
      </ListItemIcon>
      <ListItemText primary="Feedback" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<Order/>)}>
      <ListItemIcon>
        <LayersIcon />
      </ListItemIcon>
      <ListItemText primary="Order List" />
    </ListItem>

    <ListItem button onClick={()=>handleClick(<DamageFileShow/>)}>
      <ListItemIcon>
        <LayersIcon />
      </ListItemIcon>
      <ListItemText primary="Damage List" />
    </ListItem>


    <ListItem button onClick={()=>handleClick('Logout')}>
      <ListItemIcon>
        <ExitToAppIcon />
      </ListItemIcon>
      <ListItemText primary="Logout" />
    </ListItem>

  </div>
);

return(<div>{mainListItems}</div>);
}