import React, { useEffect, useState } from "react"
import {getData,BaseUrl,postData} from './FetchServices'
// import ReactToExcel from "react-html-table-to-excel"
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import { CSVLink, CSVDownload } from "react-csv";
 import excel, { readFile,XLSX } from 'xlsx';
 import Workbook from 'react-excel-workbook';
 import fs from 'file-saver';
 import Excel from "@grapecity/spread-excelio";
import { saveAs } from 'file-saver';
import { async } from "q";



const useStyles = makeStyles(theme => ({
    button: {
      margin: theme.spacing(1),
    },
    input: {
      display: 'none',
    },
    
  }));

export default function DamageFileShow(props){
    const [getlist,setList]=React.useState([])  
    const classes = useStyles();
    



const download=(file)=>{
  
console.log(file)
var url=`http://localhost:3001/damageFiles/${file}`
window.open(url)
}


    const readAllRecords=async()=>{
       // let body={'schoolId':props.schoolId}
    var list=await getData('damageFileUpload/displayall') 
    setList(list)   
    }
    const displayList=(props)=>{
     return getlist.map((item,index)=>{
     
      return(<tr><td>{item.trainerName}</td><td>{item.schoolName}</td><td>{item.currentDate}</td><td>{item.damageFileUpload}</td><td>
        
    
        <Button color='primary' onClick={(file)=>download(item.damageFileUpload)}>Download</Button>
        </td></tr>)
    
     })
    
    }


    useEffect(()=>{
    readAllRecords()    
    },[])
    return(
    
    <div>
       <center>
       
       
        
    <table border='1' id="table_id" height="60%" width="60%">
    <caption>Damage Files</caption> 
    <tbody>
    
    <tr><th>Trainer Name</th><th>School Name</th><th>Date</th><th>Damage File</th><th>Action</th></tr>    
    {displayList()}
    </tbody>   
    </table>
  
</center>

    </div>)
    
    }
    
