import React,{useEffect} from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import Button from '@material-ui/core/Button'

import MainListItems from './MainListItems';
import Avatar from '@material-ui/core/Avatar';

import {BaseUrl,getData} from '../component/FetchServices'

import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

import {blue} from '@material-ui/core/colors';
import ChartsPage from '../component/ChartsPage'
import FirstView from '../component/FirstView'


const theme = createMuiTheme({
  palette: {
    primary: {
      main:blue[500]
    },
    secondary: {
      main:blue[200]
    },
    type:"light"
  },
  status: {
    danger: 'orange',
  },

});





const drawerWidth = 210;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    
    
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
    
    
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    
    
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
   
      
      
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
      
    }),
  },
  menuButton: {
    marginRight: 36,
    
  },
  menuButtonHidden: {
    display: 'none',
    
  },
  title: {
    flexGrow: 1,
    
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
      
    }),
    
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
      
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
      
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
    
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    
  },
  fixedHeight: {
    height: 240,
  },
  avatar: {
    margin: 10,
  },
}));

export default function Dashboard(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const [view,setView]=React.useState(<FirstView/>)
 
  const [admin,setAdmin]=React.useState([])

  
  const checkSession=()=>{
    if(!localStorage.getItem('ADMIN'))
    {
     props.history.replace({pathname:'/SignIn'})
    }
    else{
      let rec=JSON.parse(localStorage.getItem('ADMIN'))
      console.log(rec)
      setAdmin(rec[0])
    }
   }
 
   useEffect(()=>{
     checkSession()
  },[])
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  const changeView=(View)=>{
   if(View=='Logout')
   {
     localStorage.clear()
     props.history.replace({pathname:'/SignIn'})
   }
   else{
     setView(View)
   }
  }
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  return (
    
    <ThemeProvider theme={theme}>
   
    <div className={classes.root}>
    
      <CssBaseline />
     
      <AppBar position="absolute" color="primary" className={clsx(classes.appBar, open && classes.appBarShift)} >
        
        <Toolbar className={classes.toolbar}>
          
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
          >
            
            <MenuIcon />
            
          </IconButton>
          
          <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
          FRS Innovation Center
          </Typography>
          <span style={{textAlign:"right"}}>
          <b>{admin.adminName} </b><br/>
          Admin
          </span>
         <Avatar src={`${BaseUrl}/images/1.jpg`} className={classes.avatar} />
        </Toolbar>
      </AppBar>
      
      <Drawer
        variant="permanent"
        
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
       
        <div  className={classes.toolbarIcon}>
         {/* company logo suitable place */}
         <img src={`${BaseUrl}/images/FIC_Icon.png`} height='80' width='100'/>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider/>
        <List ><MainListItems changeView={changeView}/></List>
        
       
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth className={classes.container}>
         


             {view}


        </Container>
        
      </main>
    </div>
    </ThemeProvider>
  );
}